import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import events from '@/components/events'//
import pingtai from '@/components/pingtaiIntroduction'//
import safety from '@/components/safety'//
import wealthTeacherList from '@/components/wealthTeacherList'//
import largeArea from '@/components/largeArea'//
import expCenterIndex from '@/components/expCenterIndex'//
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/largeArea',//
      name: 'largeArea',
      component: largeArea,
    },
    {
      path: '/expCenterIndex',//
      name: 'expCenterIndex',
      component: expCenterIndex
    },
    {
      path: '/wealthTeacherList',//
      name: 'wealthTeacherList',
      component: wealthTeacherList
    },
    {
      path: '/events',//
      name: 'events',
      component: events,
      meta: {
        title: ''
      }
    },
    {
      path: '/pingtai',//
      name: 'pingtai',
      component: pingtai,
      meta: {
        title: ''
      }
    },
    {
      path: '/safety',//
      name: 'safety',
      component: safety,
      meta: {
        title: ''
      }
    },
   /* {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }*/
  ]
})



