<script>
import { Swipe, SwipeItem } from 'mint-ui';
import Swiper from 'swiper';
import { Indicator, MessageBox, Toast } from 'mint-ui';
import axios from 'axios'
import wx from 'weixin-js-sdk';
export default {
    name:"expCenterIndex",
    data:function(){
        return{
            CallApp:false,
            isShowPage:false,
            showBanner:true,
            showbannerList:false,
            popupVisible:false,
            clickfalse:true,
            center: {lng: '', lat: ''},
            zoom: 13,
            centerName:'',
            imageURL:'',
            centerSynopsis:'',
            bannerList:[],
            userList:[],
            phone:'',
            city:'',
            areaId:'',//Id
            userName:'',
            ifBackarea:false,//
            wCardIf:'',//
            userId: '',
            photo:'', //
            mobile:'', //
            actStatus:'1',// 0 1 2
            actList:'',//
            activeShow:true,//
            activeMore:true,//
            client:'',//
            cover_maIf:false,//
            length:'',
            length2:'',
            length3:'',
            shareflag:'',
            showitem1:false,
            PlatFormInfoList:[]
        }
    },
    methods:{
        toCard(){
            if(!this.$route.query.shareUserId == false){
                var userid = this.$route.query.shareUserId
            }else{
                var userid = this.userId
            }
            var ua = navigator.userAgent.toLowerCase();
            var isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  //ios
            var isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
            if ((/micromessenger/i).test(ua)) {//isWeixinBrowser()// 
                window.location.href=this.tgHost+'?userId='+userid+'&channel=5&shareflag=1'
                return
            }else{
                window.location.href=this.tgHost+'?userId='+userid+'&channel=5'
            }
        },
        backArea:function(){
             this.$router.push({
                path:'/largeArea',
                name:'largeArea',
                query:{
                    areaId:this.areaId,//
                    userId:this.$route.query.userId,//userId;
                    shareflag:this.$route.query.shareflag,
                    shareUserId:this.$route.query.shareUserId,
                    showCard:this.$route.query.showCard,
                    shareId:this.$route.query.shareId,
                }
            })
        },
        ifShare:function(){
            var ua = navigator.userAgent.toLowerCase();
            //android
            var isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  //ios
            var isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
            if ((/micromessenger/i).test(ua)) {//isWeixinBrowser()// 
                if(this.$route.query.showCard != 0){
                    if(!this.userId == false){
                        this.wCardIf=true;//card;
                    }
                }
                //this.userName=decodeURIComponent(this.$route.query.userName);
                return
            }else{
                if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                    //ios
                    this.client = 'IOS'
                    if(!this.shareflag == false){ 
                        this.wCardIf=true;//card;
                    }else{
                        this.Share('IOS')
                    }
                } else if (/(Android)/i.test(navigator.userAgent)) {
                    //android
                    this.client == 'Android'
                    if(!this.shareflag == false){ 
                        this.wCardIf=true;//card;
                    }else{
                        this.Share('Android')
                    }
                }
            }
        },
        Share:function(cli) {
            /*
            let ua = navigator.userAgent.toLowerCase();
            //android
            let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  //ios
            let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
            if ((/micromessenger/i).test(ua)) {//isWeixinBrowser()// 
                return
            }else{
                var iconStr='[{"name":"","icon":"1","type":"html","module":"html_share","subMenu":""}]';	//tc
			   // pass("menuMessage",iconStr);//tc
                console.log(iconStr)
                if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                    //ios
                    window.webkit.messageHandlers.AppModel.postMessage({body: 'objc:///menuMessage:'+iconStr});
                } else if (/(Android)/i.test(navigator.userAgent)) {
                    //android
                    window.AndroidFunctionSetting.menuMessage(iconStr);
                }
            }*/
            var iconStr='[{"name":"","icon":"1","type":"html","module":"html_share","subMenu":""}]';	//tc
            // pass("menuMessage",iconStr);//tc
            if(cli == 'IOS'){
                window.webkit.messageHandlers.AppModel.postMessage({body: 'objc:///menuMessage:'+iconStr});
            }else{
                window.AndroidFunction.menuMessage(iconStr);
            }
        },
        timeDistance(value){
            //
            if(!value == false){
                let nowt = Math.round(new Date() / 1000)
                let times = (nowt - value / 1000);
                if(times>31536000){
                    return `${parseInt(times/31536000)}`
                }else if(times>=86400&&times<31536000){
                    return `${parseInt(times/86400)}`;
                }else if(times>=3600&&times<86400){
                    return `${parseInt(times/3600)}`;
                }else if(times>60&&times<3600){
                    return `${parseInt(times/60)}`;
                }else if(times<60){return ''}
            }
		},
          initS:function(className,num){
            className='.'+className;
             new Swiper (className, {
                slidesPerView : num,
                centeredSlides : false,
                slidesOffsetAfter : 30,//30px
                //slidesOffsetBefore : 100,//
                })     
        },
        initS2:function(className,num){
            className='.'+className;
            setTimeout(function() {
             new Swiper (className, {
                slidesPerView : num,
                spaceBetween: -20,
                centeredSlides: true,
                loop: true,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                },

            })
            },1000)     
        },
        handler ({BMap, map}) {
       // this.getCenterInfo()
        console.log(this.phone)
        var myValue = this.city;//
        setPlace();
          function setPlace(){
            map.clearOverlays();    //
            function myFun(){
              console.log(local.getResults().getPoi(0).point)
              var poi = local.getResults().getPoi(0);    //
              var lng =poi.point.lng;
              var lat =poi.point.lat;
              var point = new BMap.Point(lng,lat)
              map.centerAndZoom(point, 13)
              var marker = new BMap.Marker(point) // 
              map.addOverlay(marker) // 
              var labelContent =
                '<div style=""><p style="font-weight:600;">'
                + ""
                + '</p>'
                +'<p style="color:#6F6F6F;font-size:.32rem;white-space: normal;width:5rem;">'
                + myValue
                + '</p></div>';
            
            var label = new BMap.Label(labelContent, {offset:new BMap.Size(-50,-80)});
                    label.setStyle({
                        color : "black",
                        fontSize : ".346667rem",
                        backgroundColor :"#FFF",
                        border :"0px solid #1E90FF",
                        padding:".3rem .3rem .3rem 0",
                        boxShadow: "0px .053rem .16rem rgba(0,0,0,0.16)",
                    });
                    marker.setLabel(label); 
                var circle = new BMap.Circle(point, 6, { strokeColor: 'Red', strokeWeight: 6, strokeOpacity: 1, Color: 'Red', fillColor: '#f03' })
                map.addOverlay(circle)
            }
            var local = new BMap.LocalSearch(map, { //
              onSearchComplete: myFun
            });
            local.search(myValue);
          }
      
    },
    getClickInfo (e) {
    //   console.log(e.point.lat)
        var city=this.city
        var ua = navigator.userAgent.toLowerCase();
        var isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  //ios
        var isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
        if ((/micromessenger/i).test(ua)) {//isWeixinBrowser()// 
            const backUrl = encodeURIComponent(location.href.split('#')[0])
            axios.get('/wxservice/core/getJSSDKConfigure.mm?pageUrl='+backUrl)
                .then(function (res) {
                    // console.log(res)
                wx.config({
                    debug: false, // ,apialertpclogpc
                    appId: res.data.appId, // 
                    timestamp: res.data.timestamp, // 
                    nonceStr: res.data.nonceStr, // 
                    signature: res.data.signature, // 
                    jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage','openLocation'] // JS
                })
                wx.ready(function(res) { //ready // configready
                    wx.openLocation({
                        latitude: parseFloat(e.point.lat),//latitude
                        longitude: parseFloat(e.point.lng),//longitude
                        name: city,
                        address: city,
                        scale: 15,//
                        infoUrl: '', // ,
                        success: function (res) {
                            //   alert('');	        
                        },
                    });
                })
            })
            wx.error(function(res){//error
                // configerror
                console.log(res)
            });
        }else{
            if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                //ios
                
            } else if (/(Android)/i.test(navigator.userAgent)) {
                //android
                
            }
        }
    },
    getBanner:function(){//
        let that = this;
        Indicator.open();
        var param=Base64.encode('{"userId":"'+that.userId+'","type":"4"}');//"type": "1234"
        axios({
            method:'get',
            url:'/olmgweb/tcApiController/getTcBanner',//banner
            params:{
                param:param,
                osFlag:'3'
            }
        })
        .then(function(res) {//
            Indicator.close();
            var data=Base64.decode(res.data);
            data=jQuery.parseJSON(data);
            console.log(data)
            var retCode=data.retCode;
            var retMsg=data.retMsg;
            if(retCode == 0){
                if(data.bannerList.length>0){
                    that.showbannerList=true
                    that.bannerList=data.bannerList;
                }else{
                    that.showbannerList=false
                }
            }else{
                that.isShowPage=true
            }
        })
    },
    getCenterInfo:function({BMap, map}){
        let that = this;
        Indicator.open();
        that.groupId = that.$route.query.groupId;
        var param=Base64.encode('{"groupId":"'+that.groupId+'","userId":"'+that.userId+'"}');
        axios({
            method:'get',
            url:'/olmgweb/wzApiController/getAreaAndCenterInfo',//
            params:{
                param:param,
                osFlag:'3'
            }
        })
        .then(function(res) {//
            Indicator.close();
            var data=Base64.decode(res.data);
            data=jQuery.parseJSON(data);
            console.log(data)
            var retCode=data.retCode;
            var retMsg=data.retMsg;
            if(retCode == 0){
                if(!data.areaId==false){
                    that.ifBackarea=true;//
                    that.areaId=data.areaId
                }else{
                    that.ifBackarea=false;//
                }
                if(data.centerList.length>0){
                    console.log(data.centerList[0].centerName)
                    that.centerName=data.centerList[0].centerName//
                    if(!data.centerList[0].imageURL == false){
                        that.showBanner=true
                        that.imageURL=data.centerList[0].imageURL
                    }else{
                        that.showBanner=false
                    }
                    that.centerSynopsis=data.centerList[0].centerSynopsis//,
                    that.phone = data.centerList[0].phone
                    that.city = data.centerList[0].address
                    document.title ='  '+ that.centerName
                    that.GasyncSDKConifg(that.centerName+'','  ')
                    _czc.push(['_trackEvent', '' ,'H5DepartmentHomepage','all']);//
                    _czc.push(['_trackEvent', that.centerName+'' ,'H5DepartmentHomepage',that.centerName]);//
                }
                that.handler({BMap, map})
                
            }else{
                that.isShowPage=true
            }
        })
    },
    getuserName:function(){
        if(!this.$route.query.shareUserId == false){
            var userid = this.$route.query.shareUserId
        }else{
            var userid = this.userId
        }
        let that = this;
        Indicator.open();
        that.groupId = that.$route.query.groupId;
        var param=Base64.encode('{"groupId":"'+that.groupId+'","userId":"'+userid+'"}');
        axios({
            method:'get',
            url:'/olmgweb/wzApiController/getAreaAndCenterInfo',//
            params:{
                param:param,
                osFlag:'3'
            }
        })
        .then(function(res) {//
            Indicator.close();
            var data=Base64.decode(res.data);
            data=jQuery.parseJSON(data);
            console.log(data)
            var retCode=data.retCode;
            var retMsg=data.retMsg;
            if(retCode == 0){
                that.photo=data.photo;   //
                that.userName= data.name;   //
                that.mobile = data.mobile;  //
            }
        })
    },
    getCenterUsers:function(){
        let that = this;
        Indicator.open();
        that.groupId = that.$route.query.groupId;
        var param=Base64.encode('{"groupId":"'+that.groupId+'","condition":""}');
        axios({
            method:'get',
            url:'/olmgweb/wzApiController/getCenterUsers',//
            params:{
                param:param,
                osFlag:'3'
            }
        })
        .then(function(res) {//
            Indicator.close();
            var data=Base64.decode(res.data);
            data=jQuery.parseJSON(data);
            console.log(data)
            var retCode=data.retCode;
            var retMsg=data.retMsg;
            if(retCode == 0){
                if(data.userList.length>0){
                    that.userList=data.userList;
                }
            }else{
                that.isShowPage=true
            }
        })
    },
