        download:function(){//App
            if(!this.shareflag == false){
            var ua = navigator.userAgent.toLowerCase();
            var isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  //ios
            var isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
            if ((/micromessenger/i).test(ua)) {//isWeixinBrowser()// 
                this.CallApp=true
                window.scrollTo(0,0);
                return
            }else{
                var browser = {
                    versions: function() {
                        var u = navigator.userAgent,
                            app = navigator.appVersion;
                        return { //   
                            ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios  
                            android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc  
                            iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //iPhoneQQHD  
                        };
                    }(),      
                    language: (navigator.browserLanguage || navigator.language).toLowerCase()
                }
                var android = browser.versions.android;
                var iOS = browser.versions.ios;
                /*APP*/
                if(iOS){ 
                        window.location='tangyuanbaodatang://platformapi/startapp?param='
                        var clickedAt = +new Date;  
                        setTimeout(function(){
                            !window.document.webkitHidden && setTimeout(function(){ 
                                if (+new Date - clickedAt < 2000){  
                                    window.location = 'https://interface.tdyhfund.com/launcher/download.html?channel=app&name=dtcf';  
                                }  
                            }, 500);       
