        getActive:function(id){//
            let that = this;
            Indicator.open();
            var param={"pageNo":"1","actStatus":that.actStatus,"officeId":that.$route.query.groupId}
            console.log(param)
            axios({
                method:'get',
                url:'/wxservice/wxexternal?opName=getactiveinfo',//
                params:{
                    param:param,
                }
            })
            .then(function(res) {//
                Indicator.close();
                console.log(res.data)
                var retCode=res.data.retCode
                var itemList=res.data.itemList;
                if(retCode == 0){
                    if(itemList.length==0){
                        if(that.actStatus==1){// 0 1 2
                            that.actStatus=0;
                            that.getActive();
                                return;
                        }else if(that.actStatus==0){
                            that.actStatus=2;
                            that.getActive();
                            return;
                        }else{
                            //
                            that.activeShow=false;
                        }
                    }else if(itemList.length<=2){
                        if(itemList.length!=0 && that.actStatus==1){
                            that.showitem1=true
                        }
                    // that.activeMore=false;//
                        if(that.actStatus==1){// 0 1 2
                            that.length=itemList.length
                            that.actStatus=0;
                            that.getActive('1');
                            that.actList=itemList;
                            return;
                        }else if(that.actStatus==0){
                            that.length2=itemList.length
                            if(that.length2+that.length<=2){
                                console.log(2)
                                that.actStatus=2;
                                if(that.length2==0){
                                    that.getActive('1');
                                }
                                if(that.showitem1 == false){
                                    that.actList=itemList;
                                }
                                return;
                            }
                        }else{
                            that.length3=itemList.length
                            if(that.length2+that.length+that.length3<=2){
                                that.activeMore=false;//
                            // return;
                            }
                            that.actList=itemList;
                        }
                    }else{
                        that.activeMore=true;//
                        if(id != '1'){
                            that.actList=itemList;
                        }
                    }
                }
            })
        },
